.game-board {
    position: absolute;
    left: 0;
    top: 0;
    width: inherit;
    height: inherit;
    background-color: rgba(0, 0, 0, 0.1);
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 35px;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    visibility: hidden;
}

.game-board.active {
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.8);
}


